* {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  margin: 0px;
  padding: 0px;
}

html {
  cursor: none;
  overflow: hidden;
}

*:active #shuffle {
  transform: scale(0.35);
}

*:active #press {
  animation: rodarEdiminuir 700ms linear infinite;
}

#shuffle {
  transition: all 300ms ease;
  transform: scale(0.5);
}

#press {
  position: absolute;
  top: -35px;
  left: -40px;
  pointer-events: none;
  animation: rodarEdiminuir 150s linear infinite;
  transition: all 600ms ease;
}

.pointer {
  position: absolute;
  transform: translate(-50%, -50%)
}

#principal {
  width: auto;
  height: auto;
  z-index: 2;
  max-width: 50%;
  max-height: 70%;
  box-shadow: 0px 8px 20px 0.1px #cfcfcf;
}

#imgcontainer {
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.circulo {
  width: 20%;
  height: auto;
  position: absolute;
}

@-webkit-keyframes rotating /* Safari and Chrome */ {
  from {
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

@keyframes rotating {
  from {
    -ms-transform: rotate(0deg);
    -moz-transform: rotate(0deg);
    -webkit-transform: rotate(0deg);
    -o-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  to {
    -ms-transform: rotate(360deg);
    -moz-transform: rotate(360deg);
    -webkit-transform: rotate(360deg);
    -o-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes rodarEdiminuir {
  from {
    -ms-transform: rotate(0deg) scale(0.58);
    -moz-transform: rotate(0deg) scale(0.58);
    -webkit-transform: rotate(0deg) scale(0.58);
    -o-transform: rotate(0deg) scale(0.58);
    transform: rotate(0deg) scale(0.58);
  }
  to {
    -ms-transform: rotate(360deg) scale(0.58);
    -moz-transform: rotate(360deg) scale(0.58);
    -webkit-transform: rotate(360deg) scale(0.58);
    -o-transform: rotate(360deg) scale(0.58);
    transform: rotate(360deg) scale(0.58);
  }
}
@keyframes rodarEdiminuirAtivo {
  from {
    transform: rotate(0deg) scale(0.48);
  }
  to {
    transform: rotate(360deg) scale(0.48);
  }
}
.rodar {
  -webkit-animation: rotating 150s linear infinite;
  -moz-animation: rotating 150s linear infinite;
  -ms-animation: rotating 150s linear infinite;
  -o-animation: rotating 150s linear infinite;
  animation: rotating 150s linear infinite;
  z-index: -1;
  width: 17%;
  height: auto;
}

#circulo1 {
  left: 22%;
  top: 41%;
}

#circulo2 {
  left: 60%;
  top: 15%;
}

#texto {
  position: absolute;
  top: 10%;
  left: 0px;
}

button {
  display: inline-block;
  border: none;
  margin: 0;
  text-decoration: none;
  color: black;
  font-family: monospace;
  font-size: 4rem;
  cursor: pointer;
  text-align: center;
  transition: background 250ms ease-in-out, 
              transform 150ms ease;
  -webkit-appearance: none;
  -moz-appearance: none;
  background-color: #fff;
}

#next {
  position: absolute;
  top: 70%;
  left: 77%;
}

#previous {
  position: absolute;
  top: 75%;
  left: -5%;
  transform: rotate(270deg);
}

button:hover {
  font-style: italic;
  font-size: 5.5em;
}

button:focus {
  outline: 1px solid #fff;
  outline-offset: -4px;
}

button:active {
  transform: scale(0.99);
}

p {
  position: absolute;
  top: 90%;
  left: 60%;
  font-family: monospace;
}


